.home-container {
  padding: 0px 20px 00px 20px !important;
  display: flex;
  align-items: center;
  margin: 0 !important;
  max-width: 100% !important;
  height: 100% !important;
}

.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  width: 100%;
  justify-content: center;
}

.nav {
  margin: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-link:hover {
  transform: translateY(-7px);
}

hr {
  -webkit-animation: lineDraw 1s;
  -moz-animation: lineDraw 1s;
  -o-animation: lineDraw 1s;
  animation: lineDraw 1s;
  animation-fill-mode: forwards;
}

@keyframes lineDraw {
  100% {
    width: 50%;
  }
}
